import { Ref, Computed } from '@linkorgs/reactivity'
import { isArray, isDNode, isString } from '../shared'
import { Component } from './component'

export type RenderElement = Text | Element
export type TextContent = string | number | boolean | null | undefined
export type Content = TextContent | Ref | Computed | Content[]
export type Children = DNode[] | DNode | Content
export interface DNode<T extends DNodeTypes = any> {
  _is_dnode: boolean
  type?: T
  _el?: RenderElement
  _parent?: RenderElement
  _anchor?: RenderElement
  _root?: RenderElement
  key?: number | string | Symbol
  parent?: DNode
  tag?: any
  context?: Partial<Context>
  contextCache?: Map<string, any>
  children?: Children | Children[]
  is?: any
  component?: Component
}
export const Text = Symbol('Text')
export const Element = Symbol('Element')
export const Comment = Symbol('Comment')
export const Static = Symbol('Static')
export const Fragment = Symbol('Fragment')
export type DNodeTypes =
  | string
  | DNode
  | Component
  | typeof Text
  | typeof Element
  | typeof Static
  | typeof Comment
  | typeof Fragment

export interface Context {
  props: {
    [k: string]: any
  }
  provide: {
    [k: string]: Ref
  }
  inject: string[]
  on: {
    [k: string]: any
  }
}

export function createDNode(
  tag: string,
  context?: Partial<Context>,
  children?: Children | Children[]
): DNode {
  return {
    type: Element,
    _is_dnode: true,
    tag,
    context,
    children,
  }
}

export function createComponent(tag: string, props?: any, children?: Children | Children[]): DNode {
  const context = props ? { props } : null
  if (isString(tag)) {
    // global register component
    return createDNode(tag, context, children)
  }
  return {
    type: tag,
    _is_dnode: true,
    context: context,
    children,
  }
}

export function createFragment(dnode: DNode | DNode[]): DNode {
  const fragment: DNode = {
    type: Fragment,
    _is_dnode: true,
    children: dnode,
  }
  return fragment
}

export function createText(dnode: TextContent): DNode {
  const text: DNode = {
    type: Text,
    _is_dnode: true,
    children: dnode,
  }
  return text
}

export function createDNodeWithType(
  type: DNodeTypes,
  ctx?: Context,
  children?: Children | Children[]
): DNode {
  return {
    type,
    context: ctx,
    children: children,
    _is_dnode: true,
  }
}

export function normalizeDNode(child: Children | Children[]): DNode {
  if (isDNode(child)) {
    return child
  }
  if (child == null || typeof child === 'boolean') {
    return createDNodeWithType(Comment)
  } else if (Array.isArray(child)) {
    // fragment
    return createDNodeWithType(Fragment, null, child.slice())
  } else {
    return createDNodeWithType(Text, null, String(child))
  }
}

export function normalizeChildrenDNode(dnode: DNode) {
  let children = isArray(dnode.children) ? dnode.children : [dnode.children]

  for (let i = 0; i < children.length; i++) {
    const node = normalizeDNode(children[i])
    node.parent = dnode
    if (!node._parent && (dnode._el || dnode._parent)) {
      node._parent = dnode._el || dnode._parent
    }
    children[i] = node
  }

  return children
}
