// TODO 拆分成两部分 runtime-dom 和 runtime-core

import { isString } from '../shared'
import { DNode } from './dnode'
import { patch } from './dom/renderer'

export function render(dnode: DNode, container: Element) {
  patch(null, dnode, container)
}

interface Instance {
  container: Element | null
  mount: (containerOrSelector: string | HTMLElement) => void
  unmount: () => void
}

export function createApp(dnode: DNode) {
  const instance: Instance = {
    container: null,
    mount(containerOrSelector) {
      if (containerOrSelector) {
        if (isString(containerOrSelector)) {
          instance.container = document.querySelector(containerOrSelector)!
        } else {
          instance.container = containerOrSelector
        }
      }

      if (!instance.container) return

      if (instance.container) {
        // clear html
        instance.container.innerHTML = ''

        render(dnode, instance.container)
      }
    },
    unmount() {
      if (instance.container) {
        instance.container.innerHTML = ''
      }
    },
  }

  return instance
}
