import { reactive } from './reactive'

export class Ref<T = any> {
  public _value: { value: T }
  public _is_ref: boolean

  constructor(value: T) {
    this._value = reactive<{ value: T }>({
      value,
    })
    this._is_ref = true
  }

  get value() {
    return this._value.value
  }

  set value(v) {
    this._value.value = v
  }
}

export function ref<T = any>(value: T) {
  return new Ref(value)
}
