import { isArray } from '../utils'

let activeEffect: ReactiveEffect | undefined

export type Dep = Set<ReactiveEffect>
export type EffectScheduler = (...args: any[]) => any
export const targetsMap = new WeakMap<any, Map<any, Dep>>()

export const enum TriggerOpTypes {
  SET = 'set',
  ADD = 'add',
  DELETE = 'delete',
  CLEAR = 'clear',
}

export class ReactiveEffect<T = any> {
  public deps: Dep[] = []
  public parent: ReactiveEffect | undefined = undefined

  constructor(public fn: () => T, public scheduler: EffectScheduler | null = null) {}

  run() {
    let parent: ReactiveEffect | undefined = activeEffect
    let lastShouldTrack = shouldTrack
    while (parent) {
      if (parent === this) {
        return
      }
      parent = parent.parent
    }
    try {
      this.parent = activeEffect
      activeEffect = this
      shouldTrack = true

      return this.fn()
    } finally {
      activeEffect = this.parent
      this.parent = undefined
      shouldTrack = lastShouldTrack
    }
  }
}

export function effect<T = any>(fn: (...args: any) => T) {
  const _effect = new ReactiveEffect(fn)
  _effect.run()
  const runner = _effect.run.bind(_effect)
  return runner
}

export let shouldTrack = true
const trackStack: boolean[] = []

export function pauseTracking() {
  trackStack.push(shouldTrack)
  shouldTrack = false
}

export function enableTracking() {
  trackStack.push(shouldTrack)
  shouldTrack = true
}

export function resetTracking() {
  const last = trackStack.pop()
  shouldTrack = last === undefined ? true : last
}

export function track(target: object, key: string | symbol) {
  if (shouldTrack && activeEffect) {
    let depsMap = targetsMap.get(target)
    if (!depsMap) {
      targetsMap.set(target, (depsMap = new Map()))
    }
    let deps = depsMap.get(key)
    if (!deps) {
      depsMap.set(key, (deps = new Set()))
    }

    trackEffect(deps)
  }
}

export function trackEffect(dep: Dep) {
  if (activeEffect && !dep.has(activeEffect)) {
    dep.add(activeEffect)
    activeEffect.deps.push(dep)
  }
}

export function trigger(target: object, key: string | symbol, type: TriggerOpTypes) {
  const depsMap = targetsMap.get(target)
  if (depsMap) {
    const deps: Dep[] = []
    if (key === 'length' && isArray(target)) {
      depsMap.forEach((dep, key) => {
        if (key === 'length') {
          deps.push(dep)
        }
      })
    } else {
      deps.push(depsMap.get(key))

      switch (type) {
        case TriggerOpTypes.ADD:
          // console.log(depsMap)
          break
        case TriggerOpTypes.SET:
          // console.log(depsMap)
          break
      }
    }
    if (deps.length === 1) {
      if (deps[0]) {
        triggerEffect(deps[0])
      }
    } else {
      const effects: ReactiveEffect[] = []
      for (const dep of deps) {
        if (dep) {
          effects.push(...dep)
        }
      }
      triggerEffect(new Set(effects))
    }
  }
}

export function triggerEffect(dep: Dep | ReactiveEffect[]) {
  for (const effect of isArray(dep) ? dep : [...dep]) {
    if (effect.scheduler) {
      effect.scheduler()
    } else {
      effect.run()
    }
  }
}
