import { hasChanged, hasOwn, isArray, isIntegerKey } from '../utils'
import { pauseTracking, resetTracking, track, trigger, TriggerOpTypes } from './effect'

export const reactiveMap = new WeakMap<Target, any>()

export interface Target {
  [ReactiveFlags.IS_REACTIVE]?: boolean
  [ReactiveFlags.IS_READONLY]?: boolean
  [ReactiveFlags.RAW]?: any
}

export const enum ReactiveFlags {
  IS_REACTIVE = '__v_isReactive',
  IS_READONLY = '__v_isReadonly',
  RAW = '__v_raw',
}

const arrayInstrumentations = createArrayInstrumentations()

function createArrayInstrumentations() {
  const instrumentations: Record<string, Function> = {}
  ;(['push', 'pop', 'shift', 'unshift', 'splice'] as const).forEach((key) => {
    instrumentations[key] = function (this: any, ...args: unknown[]) {
      pauseTracking()
      const res = (this[ReactiveFlags.RAW] as any)[key].apply(this, args)
      resetTracking()
      return res
    }
  })
  return instrumentations
}

function get(target: object, key: string | symbol, receiver: object) {
  if (key === ReactiveFlags.IS_REACTIVE && reactiveMap.get(target)) {
    return true
  }
  if (key === ReactiveFlags.RAW) {
    return target
  }

  const targetIsArray = Array.isArray(target)

  if (targetIsArray) {
    if (hasOwn(arrayInstrumentations, key)) {
      // 跳过一些内置操作，比如splice会分为两步完成。
      // 第一步把后面的数值拷贝填充的删除下标
      // 第二步则是修改length删除多余的部分
      return Reflect.get(arrayInstrumentations, key, receiver)
    }
  }

  const res = Reflect.get(target, key, receiver)

  track(target, key)

  if (res && typeof res === 'object') {
    // deep reactive
    return reactive(res)
  }

  return res
}

function set(target: object, key: string | symbol, value: unknown, receiver: object) {
  const res = Reflect.set(target, key, value, receiver)

  trigger(target, key, TriggerOpTypes.ADD)

  return res
}

function has(target: object, key: string | symbol) {
  const res = Reflect.has(target, key)
  return res
}

function deleteProperty(target: object, key: string | symbol) {
  return true
}

function ownKeys(target: object) {
  const res = Reflect.ownKeys(target)
  return res
}

function apply(target: Function, thisArg: any, argArray: any[]) {
  const res = target.apply(thisArg, argArray)
  return res
}

const mutableHandlers: ProxyHandler<object> = {
  get,
  set,
  has,
  deleteProperty,
  ownKeys,
  apply,
}

export function reactive<T extends object>(target: T): T {
  const existingProxy = reactiveMap.get(target)
  if (existingProxy) {
    return existingProxy
  }

  const proxy = new Proxy<T>(target, mutableHandlers)
  reactiveMap.set(target, proxy)

  return proxy
}
