import { callWithErrorHandling } from './renderComponentUtil'

export interface SchedulerJob extends Function {
  id: number
}

const queue: SchedulerJob[] = []
const resolvedPromise: Promise<any> = Promise.resolve()
let currentJobPromise: Promise<any>

export function nextTick(fn?: () => void) {
  const p = currentJobPromise || resolvedPromise
  return fn ? p.then(this ? fn.bind(this) : fn) : p
}

export function queueJob(job: SchedulerJob) {
  if (job.id === null) {
    queue.push(job)
  } else {
    // 按照注册顺序依次执行更新
    // Parent -> Children
    queue.splice(
      queue.findIndex((x) => x.id === job.id),
      0,
      job
    )
  }

  currentJobPromise = resolvedPromise.then(flushJobs)
}

export function flushJobs() {
  try {
    queue.sort((a, b) => a.id - b.id)
    for (let i = 0; i < queue.length; i++) {
      const job = queue[i]
      callWithErrorHandling(job, null, 'scheduler')
    }
  } finally {
    queue.length = 0
  }
}
