import { ReactiveEffect } from '@linkorgs/reactivity'
import { haveValue, isArray } from '../../shared'
import { createComponentInstance, Component, renderComponent } from '../component'
import { createElement, createText, insert, remove } from './opt'
import { queueJob, SchedulerJob } from '../scheduler'
import {
  DNode,
  RenderElement,
  Fragment,
  normalizeDNode,
  Text,
  Element,
  Children,
  normalizeChildrenDNode,
  Comment,
} from '../dnode'
import { mountContext, patchContext } from './context'
import { normalizeComponent } from '../renderComponentUtil'
import { warn } from '../../shared/assert'

function mountText(dnode: DNode<typeof Text>, container: RenderElement, _anchor: RenderElement) {
  const textNode = createText(dnode.children as string)

  dnode._el = textNode
  dnode._parent = container

  insert(textNode, container, _anchor)
}

function patchText(n1: DNode<typeof Text>, n2: DNode<typeof Text>) {
  const c1 = String(n1.children)
  const c2 = String(n2.children)
  const el = (n2._el = n1._el)

  if (c1 !== c2) {
    el.textContent = c2
  }
}

function mountChildrenNode(
  node: DNode,
  container: RenderElement,
  _anchor: RenderElement,
  parentComponent: Component | null = null
) {
  patch(null, node, container, _anchor, parentComponent)
}

function mountChildren(
  nodes: DNode[] | Children[],
  container: RenderElement,
  _anchor: RenderElement,
  start = 0
) {
  for (let i = start; i < nodes.length; i++) {
    const node = normalizeDNode(nodes[i])
    nodes[i] = node
    mountChildrenNode(node, container, _anchor)
  }
}

function unmount(dnode: DNode) {
  const { _el, _parent } = dnode

  if (_parent && _parent.contains(_el)) {
    remove(_el, _parent)
  } else {
    warn('parent not found. el is ', _el)
  }
}

function unmountChildren(c1: DNode[], start = 0) {
  for (let i = start; i < c1.length; i++) {
    unmount(c1[i])
  }
}

function patchUnkeyedChildren(
  c1: DNode[],
  c2: DNode[],
  container: RenderElement,
  _anchor: RenderElement
) {
  const oldLength = c1.length
  const newLength = c2.length
  const commonLength = Math.min(oldLength, newLength)
  for (let i = 0; i < commonLength; i++) {
    const nextChild = normalizeDNode(c2[i])
    patch(c1[i], nextChild, container, _anchor)
  }
  if (oldLength > newLength) {
    // remove old
    unmountChildren(c1, commonLength)
  } else {
    // mount new
    mountChildren(c2, container, _anchor, commonLength)
  }
}
function patchKeyedChildren() {}

function patchChildren(
  n1: DNode,
  n2: DNode,
  container: RenderElement,
  _anchor: RenderElement,
  parentComponent: Component
) {
  n1.children = normalizeChildrenDNode(n1)
  n2.children = normalizeChildrenDNode(n2)

  const c1 = n1.children as DNode[]
  const c2 = n2.children as DNode[]

  if (isArray(c1) && isArray(c2)) {
    if (haveValue(c2[0]?.key)) {
      patchKeyedChildren()
    } else {
      // fragment 没有 dom 实例时，用 container
      const el = (n2._el = n1._el)
      patchUnkeyedChildren(c1, c2, el || container, _anchor)
    }
  }

  if (!isArray(c1) && !isArray(c2)) {
    const oldDNode = normalizeDNode(c1)
    const newDNode = normalizeDNode(c2)
    if (!oldDNode._el) {
      // 继承
      oldDNode._el = n1._el
    }
    patch(oldDNode, newDNode, container, _anchor, parentComponent)
  }
}

function mountElement(dnode: DNode, container: RenderElement, _anchor: RenderElement) {
  const { tag, is } = dnode
  const el = createElement(tag, is)

  dnode._el = el
  dnode._parent = container

  mountContext(el, dnode)

  if (haveValue(dnode.children)) {
    dnode.children = normalizeChildrenDNode(dnode)
    mountChildren(dnode.children, el, _anchor)
  }

  insert(el, container, _anchor)

  return el
}

function patchElement(
  n1: DNode,
  n2: DNode,
  container: RenderElement,
  _anchor: RenderElement,
  parentComponent: Component
) {
  const el = (n2._el = n1._el!)
  if (n1.type !== n2.type) {
    // 类型发生变更，卸载之前的节点，挂载新的节点
    unmount(n1)
    mountElement(n2, container, _anchor)
  } else {
    patchChildren(n1, n2, container, _anchor, parentComponent)
    patchContext(el as HTMLElement, n1, n2)
  }
}

function renderComponentEffect(component: Component) {
  function updateComponentEffect() {
    if (component.mounted) {
      const prevTree = component.subTree
      const nextTree = renderComponent(component)
      component.subTree = nextTree
      const { _parent, _el, _anchor } = prevTree
      if (_parent || _el) {
        patch(prevTree, nextTree, _parent || _el, _anchor, component)
      } else {
        warn('container not found', prevTree, nextTree)
      }
    } else {
      const { dnode } = component
      const { _parent, _anchor } = dnode
      const tree = renderComponent(component)
      component.subTree = tree
      patch(null, tree, _parent, _anchor, component)
    }
  }

  const effect = (component.effect = new ReactiveEffect(updateComponentEffect, () =>
    queueJob(component.update)
  ))

  const update = (component.update = effect.run.bind(effect) as SchedulerJob)
  update.id = component.uid
  update()
}

function mountComponent(
  dnode: DNode,
  container: RenderElement,
  _anchor: RenderElement,
  parentComponent: Component
) {
  const instance = (dnode.component = createComponentInstance(dnode, parentComponent))

  if (!dnode._root) {
    dnode._root = container
  }

  renderComponentEffect(instance)
}

function patchComponent(n1: DNode, n2: DNode) {
  const component = (n2.component = n1.component)!
  if (n1.type !== n2.type) {
    // true ? <componentA /> : <componentB />
    component.type = n2.type
  }
  if (n2.context) {
    component.props = n2.context.props
  }
  component.dnode = n2

  normalizeComponent(n2, component)

  component.update()
}

function processComponent(
  n1: DNode | null,
  n2: DNode,
  container: RenderElement,
  _anchor: RenderElement,
  parentComponent: Component
) {
  if (n1 === null) {
    n2._parent = container
    mountComponent(n2, container, _anchor, parentComponent)
  } else {
    patchComponent(n1, n2)
  }
}

function processElement(
  n1: DNode | null,
  n2: DNode,
  container: RenderElement,
  _anchor: RenderElement,
  parentComponent: Component
) {
  if (n1 === null) {
    mountElement(n2, container, _anchor)
  } else {
    patchElement(n1, n2, container, _anchor, parentComponent)
  }
}

function processFragment(
  n1: DNode | null,
  n2: DNode,
  container: RenderElement,
  _anchor: RenderElement,
  parentComponent: Component
) {
  // const fragmentStartAnchor = (n2._el = n1 ? n1._el : createText(''))!
  const fragmentEndAnchor = (n2._anchor = n1 ? n1._anchor : createText(''))!
  if (n1 === null) {
    n2._anchor = fragmentEndAnchor
    // insert(fragmentStartAnchor, container, _anchor)
    insert(fragmentEndAnchor, container, _anchor)

    n2.children = normalizeChildrenDNode(n2)
    mountChildren(n2.children, container, fragmentEndAnchor)
  } else {
    patchChildren(n1, n2, container, fragmentEndAnchor, parentComponent)
  }
}

function processComment(
  n1: DNode | null,
  n2: DNode,
  container: RenderElement,
  _anchor: RenderElement
) {}

function processText(
  n1: DNode | null,
  n2: DNode,
  container: RenderElement,
  _anchor: RenderElement
) {
  if (n1 === null) {
    mountText(n2, container, _anchor)
  } else {
    patchText(n1, n2)
  }
}

export function patch(
  n1: DNode | null,
  n2: DNode,
  container: RenderElement,
  anchor: RenderElement | null = null,
  parentComponent: Component | null = null
) {
  const { type } = n2
  switch (type) {
    case Comment:
      processComment(n1, n2, container, anchor)
      break
    case Text:
      processText(n1, n2, container, anchor)
      break
    case Element:
      processElement(n1, n2, container, anchor, parentComponent)
      break
    case Fragment:
      processFragment(n1, n2, container, anchor, parentComponent)
      break
    default:
      processComponent(n1, n2, container, anchor, parentComponent)
  }
}
