import { reactive } from '@linkorgs/runtime'

export default function () {
  const state = reactive({
    count: 0,
    checked: false,
  })
  return () => (
    <div className="block text-left">
      <h6>
        下面是使用 <em className="tag">Do</em> 这个框架制作的一些 Demo
      </h6>
      <ul>
        <li>
          <div className="flex items-center">
            这是一个累加器按钮
            <button style={{ margin: '4px' }} onClick={() => state.count++}>
              {state.count}
            </button>
          </div>
        </li>
        <li>
          <div className="flex items-center">
            这是一个 checkbox 选择框 （{state.checked ? '开' : '关'}）
            <label class="switch">
              <input
                type="checkbox"
                onChange={() => {
                  state.checked = !state.checked
                }}
                checked={state.checked}
              />
              <span class="slider round"></span>
            </label>
          </div>
        </li>
      </ul>
    </div>
  )
}
