export function insert(child: Node, parent: any, anchor?: Node) {
  if (anchor && parent.contains(anchor)) {
    parent.insertBefore(child, anchor)
  } else {
    append(child, parent)
  }
}

export function append(child: Node, parent: any) {
  parent.appendChild(child)
}

export function remove(child: Node, parent: any) {
  parent.removeChild(child)
}

export function createElement(tag: keyof HTMLElementTagNameMap, is?: any) {
  return document.createElement(tag, is)
}

export function createText(text: string) {
  return document.createTextNode(text)
}

export function createComment(data?: string) {
  return document.createComment(data)
}

export function setText(node: Node, text: string) {
  node.nodeValue = text
}

export function querySelector(selector: string) {
  return document.querySelector(selector)
}
