import { warn } from '../../shared/assert'
import { DNode } from '../dnode'

export type DNodeOn = Record<string, any>

function diffArray(a1: string[], a2: string[]) {
  return a1.filter((x) => a2.indexOf(x) < 0)
}

export function patchEvent(el: HTMLElement, oldEvents: DNodeOn, newEvents: DNodeOn) {
  if (newEvents) {
    for (const key in newEvents) {
      if (oldEvents && oldEvents[key]) {
        if (oldEvents[key] === newEvents[key]) {
          // on: {click: 'a'}
          // on: {click: 'a'}
          continue
        } else {
          // on: {click: 'a'}
          // on: {click: 'b'}
          el.removeEventListener(key, oldEvents[key])
          el.addEventListener(key, newEvents[key])
        }
      } else {
        // on: {}
        // on: {click: 'b'}
        el.addEventListener(key, newEvents[key])
      }
    }
  }
}

function patchClass(el: HTMLElement, value: string) {
  if (value == null) {
    el.removeAttribute('class')
  } else {
    el.className = value
  }
}

export function patchProps(el: HTMLElement, key: string, oldValue?: any, newValue?: any) {
  if (key === 'style') {
    for (const [k, v] of Object.entries(newValue)) {
      el.style[k] = v
    }
  } else if (key === 'class') {
    patchClass(el, newValue)
  } else {
    try {
      el[key] = newValue
    } catch (e) {
      warn(`patchProps key ${key}, value ${newValue}`)
    }
  }
}

export function mountContext(el: HTMLElement, dnode: DNode) {
  if (!dnode.contextCache) {
    dnode.contextCache = new Map()
  }
  if (dnode.context) {
    if (dnode.context.on) {
      patchEvent(el, null, dnode.context.on)
    }
    if (dnode.context.props) {
      const props = dnode.context.props
      for (const key in props) {
        patchProps(el, key, null, props[key])
      }
    }
  }
}

export function patchContext(el: HTMLElement, n1: DNode, n2: DNode) {
  for (const key in n2.context) {
    if (key === 'on') {
      patchEvent(el, n1.context?.on, n2.context?.on)
    }
    if (key === 'props') {
      for (const prop in n2.context.props) {
        const oldValue = n1.context?.props?.[prop]
        const newValue = n2.context?.props?.[prop]
        patchProps(el, prop, oldValue, newValue)
      }
    }
  }
}
